body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 0px;
  margin: 0px;
  background-color: #001;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
}

.loadingItem{
  margin-left: auto;
  margin-right: auto;
  color: #fff;
}

.loadingItem p {
  font-size: calc(7px + 1vmin);
}

.LoadingPage{
  background-color: #435;
  width: 100%;
  height: 100%;
  text-align: center;
  position: absolute;
  color: #fff;
  line-height: 520px;
}

.insertProduto {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 2%;
}

.view {
  padding: calc(2% + 2vmin);
  background-color: #001;
  color: #fff;
  cursor:default;
}

.view div img{
  width: calc(100px + 20vmin);
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-left: auto;
  margin-right: auto;
}

.view div h1 {
  font-size: calc(30px + 0.1vmin);
}

.view div p {
  width: 90%;
  font-size: calc(10px + 2vmin);
}

.view .socialIcon{
  width: 40px;
  height: 40px;
  padding: 5px;
  display: inline-block;
  border-radius: 10px;
}

.view .More p {
  font-size: calc(8px + 1vmin);
}

.homeScreen {
  padding: calc(2% + 0.1vmin);
}

.homeScreen h1 {
  font-size: calc(46px + 2vmin);
  padding: calc(2% + 0.1vmin);
  color: #fff;
}

.produto {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-align-items:stretch;
          align-items:stretch;
  font-size: calc(10px + 2vmin);
}

.produto a figure {
  display: -webkit-flex;
  display: flex;
  width: calc(220px + 0.1vmin);
  height: calc(180px + 0.1vmin);
  background-color: #ddd;
  padding: calc(2% + 1vmin);
  border-radius: calc(20px + 0.1vmin);
  transition: 0.3s;
}

.produto a figure:hover {
  transition: 0.3s;
  box-shadow: 0px 0px 0px 5px #fff;
  background-color: #fff;
  color: #435;
}

.produto a figure img {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: calc(5% + 0.1vmin);
  width:  calc(100px + 0.1vmin);
  height:  calc(150px + 0.1vmin);
}

.produto a figure h1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  font-size: calc(14px + 0.2vmin);
  color: #000;
}

.produto a figure h1:hover{
  color: #dd0;
}

.produtos h1 {
  background-color: #000;
  color: #fff;
  width: 100%;
}

button{
  padding: 5px;
  background-color: #435;
  border: none;
  border-radius: 8px;
  color: #fff;
  margin-left: auto;
  margin-right: auto;
  display: block;
  cursor: pointer;
  transition: 0.3s;
}

button:hover{
  background-color: #647;
  transition: 0.3s;
}

Footer{
  padding: calc(5% + 0.2vmin);
  color: #fff;
  text-align: center;
  font-size: calc(11px + 0.2vmin);
}

